import React, { useContext, useEffect, useState } from 'react';
import RecordContext from '../../context/record';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { useNavigate } from "react-router-dom"



import {
	Chart as ChartJS,
	CategoryScale,
	LinearScale,
	BarElement,
	Title,
	Tooltip,
	Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import { IconButton, TextField, Typography } from '@material-ui/core';

ChartJS.register(
	CategoryScale,
	LinearScale,
	BarElement,
	Title,
	Tooltip,
	Legend
);


function Analytics({ graphType = "LINE" }) {
	const [data, setData] = useState([]);
	const [n, setN] = useState(7);
	const navigate = useNavigate()


	const options = {
		responsive: true,
		plugins: {
			legend: {
				position: 'top',
			},
			title: {
				display: true,
				text: `Displaying analytics of last ${n} days`,
			},
		}
	};

	const { getAnalyticsData } = useContext(RecordContext)

	useEffect(() => {
		const { data } = getAnalyticsData(n)
		console.log(data)
		setData(data)
	}, [n, graphType])
	return (
		<div style={{ padding: 30 }}>
			<IconButton style={{ width: 50, margin: 10 }}>
				<ArrowBackIcon onClick={() => navigate("/study-logger")} />
			</IconButton>
			<Line options={options} data={data} />
		</div>
	)
}

export default Analytics